<template>
  <!-- <div class="main-wrapper"> -->
    <div class="main-page white-back">
      <div class="center-container">
        <div class="result-header">
          Ваш результат:
        </div>
        <div class="result-body">
          <div class="result-left-section">
            <div class="result-text-points">
              Вы набрали {{ totalPoints }} баллов из {{ maxTotalPoints }} возможных
            </div>
            <div
              v-html="resultText"
              class="result-text"
            >              
            </div>
          </div>
          <div class="result-right-section">
            <img
              class="result-image"
              :src="getImage"
              alt="">
          </div>
        </div>

        <div class="footer-section">
          <div
            @click = newTest
            class="footer-button">
            {{ percentList[indexPercent].buttonText}}
          </div>
        </div>
      </div>
    </div>

  <!-- </div> -->
</template>
  
<script>
  import { mapActions, mapGetters} from 'vuex'
  import axios from 'axios'

  export default {
    data() {
      return {
        totalPoints: 0,
        indexPercent: 0,
        maxTotalPoints: 26,
        percentList: [
          {
            minPercent: 0,
            maxPercent: 57,
            text: "Поздравляем с завершением ведения пациентки с синдромом короткой кишки! <br>Вы принимали ответственные решения по выбору тактики лечения, назначению лабораторно-инструментальных исследований и применению патогенетической терапии. Обратите внимание, что многие этапы ведения пациентки можно было осуществить иным, более оптимальным путём.",
            buttonText: "Улучшить результат",
            resultImage: "place3.svg",
          },
          {
            minPercent: 58,
            maxPercent: 81,
            text: "Поздравляем с завершением ведения пациентки с синдромом короткой кишки! <br>Во многих случаях вы принимали верные решения по тактике лечения. Однако обратите внимание, что некоторые этапы ведения пациентки можно было осуществить более оптимальным путём.",
            buttonText: "Улучшить результат",
            resultImage: "place2.svg",
          },
          {
            minPercent: 82,
            maxPercent: 100,
            text: "Поздравляем с успешным ведением пациентки с синдромом короткой кишки! <br>Вы принимали наиболее оптимальные решения в выборе тактики лечения, назначении лабораторно-инструментальных исследований и применении патогенетической терапии.",
            buttonText: "Пройти еще раз",
            resultImage: "place1.svg",
          }
        ]        
      }
    },
    computed: {
      ...mapGetters([
          "questionStack",
          "serverURL"
      ]),
      resultText(){
        return this.percentList[this.indexPercent].text
      },
      getImage(){
        let imageName = this.percentList[this.indexPercent].resultImage
        return require(`@/assets/images/${imageName}`)
      }
    },
    methods: {
    ...mapActions([
        "GET_QUESTION_DATA",            
        "newTest"
    ]),

    getTotalPoints() {      
      for (let i = 0; i < this.questionStack.length; i++) {
        this.GET_POINTS_FROM_DATA(this.questionStack[i])
      }
    },

    increaseTotalPoints(nodePoints){
      this.totalPoints += nodePoints
    },

    GET_POINTS_FROM_DATA(questionId){
      axios({
        method: 'get',
        url: `${this.serverURL}/question/${questionId}`,
      })
      .then((questionData) => {
        if (questionData.data.data.body.nodePoints) {
          this.increaseTotalPoints(questionData.data.data.body.nodePoints)          
        }
      })        
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },
    },

    watch: {
      totalPoints(newPoints) {
        let currentPercent = Math.round(newPoints / this.maxTotalPoints*100)
        const index = this.percentList.findIndex( x => currentPercent >= x.minPercent && currentPercent <= x.maxPercent)
      
      // let currentPercent = Math.round(newPoints / this.maxTotalPoints*100)
      // const index = this.percentList.findIndex( x => currentPercent >= x.minPercent && currentPercent <= x.maxPercent)

        if (index !== -1) {
          this.indexPercent = index
        }
      }
    },

    mounted() {
      this.getTotalPoints()
    }

  }
</script>

