<template>
  <!-- <div class="main-wrapper"> -->
    
    
    <div class="main-page">
      <div class="center-container">
      <TextContent
        v-if="infoText"
        :text="infoText"
        class="text-section"
        :class="{'text-without-answers': answerTransitions.length < 2}"
      />
      <!-- <TextContent
        v-if="questionText"
        :text="questionText"
        class="text-section"
      /> -->

      <div
        v-if="answerTransitions.length > 1"
        class="answer-section"
      >
        <div
          v-for="answer, index in answerTransitions"
          :key="index"
          class="answer-item"
          :class="{'answer-item-checked':pickedTransitionId===answer.transitionId}"
        >          
          <input
            type="radio" 
            :id="index" 
            :value="answer.transitionId" 
            v-model="pickedTransitionId" 
            class="answer-item-input"
            />
          <label
            :for="index"
            class="answer-label"
            >
            <div class="answer-item-new-input">
              <div class="circle-button">
                <div
                  v-if="pickedTransitionId===answer.transitionId"
                  class="circle-button-point"
                >
                </div>
              </div>
            </div>
            <div class="answer-label-text">
              {{ answer.answerText }}
            </div>
            
          </label>
        </div>
      </div>

      <!-- <FooterButtons
        class="footer-section"
      /> -->

      <div class="footer-section">
        
        <div
          v-if="questionStack.length > 1"
          @click="previousQuestionAndClearPicked"
          class="footer-button"
        >
          Назад
        </div>
        <div
          v-if="answerTransitions.length > 0"
          @click="nextQuestionAndClearPicked"
          class="footer-button"
        >
          Далее
        </div>
        <router-link to="/resume">
          <div
            v-if="answerTransitions.length === 0"
            @click="increaseBackgroundId"
            class="footer-button"
          >
            Перейти к результатам
          </div>
        </router-link>
      
      </div>

    </div>

    </div>

    <ContinueModal
      v-if="showContinueModal"
      @checkContinueTest = checkContinueTest($event)
    />

  <!-- </div> -->
</template>

<script>
import { mapActions, mapGetters} from 'vuex'
import TextContent from '@/components/TextContent.vue'
import ContinueModal from '@/components/ContinueModal.vue'

export default {
    data() {
      return {
        pickedTransitionId: "",
        showContinueModal: false,
        questionListFromLocalStorage: []
      }
    },
    components:{
        TextContent,
        ContinueModal
    },
    computed: {
      ...mapGetters([
        "testId",
        "questionId",
        "parentId",
        "infoText",
        "questionText",
        "answerTransitions",
        "questionStack",
        "startQuestionId"
      ])
      
    },
    methods: {
      ...mapActions([
        "GET_QUESTION_DATA",
        "setCurrentTransitionId",
        "nextQuestion",
        "previousQuestion",
        "continueTest",
        "newTest",
        "increaseBackgroundId"
      ]),
      previousQuestionAndClearPicked(){
        this.pickedTransitionId = ""
        this.previousQuestion()
      },
      nextQuestionAndClearPicked(){
        this.pickedTransitionId = ""
        this.nextQuestion()
      },

      checkLocalStorage(){
        const currentQuestionListRow = localStorage.getItem(`questionList${this.testId}`)
        if (currentQuestionListRow) {
          const currentQuestionList = JSON.parse(currentQuestionListRow)

          if (currentQuestionList.length > 1) {
            this.questionListFromLocalStorage = currentQuestionList
            this.openContinueModal()
          }
          else{
            this.createLocalStorage()
          }
        }
        else {          
          this.createLocalStorage()
        }
      },
      createLocalStorage(){
        localStorage.setItem(`questionList${this.testId}`, JSON.stringify([this.startQuestionId]))
        this.GET_QUESTION_DATA()
      },
      openContinueModal(){
        this.showContinueModal = true
      },
      checkContinueTest(isContinue){
        this.showContinueModal = false
        if (isContinue) {
          this.continueTest(this.questionListFromLocalStorage)
        }
        else {
          this.newTest()
        }
      },
      // toResume() {
      //   this.increaseBackgroundId()
      //   this.$router.push("/resume")
      // }

    },
    mounted() {
        this.checkLocalStorage()
        // this.GET_QUESTION_DATA()
        this.pickedTransitionId = ""
    },
    watch: {
      pickedTransitionId(newTransitionId){
      this.setCurrentTransitionId(newTransitionId)
    }
  }
}

</script>

